/* Pagina LotoMania styles.css */
.menu {
  display: flex;
  gap: 10px;
  justify-content: center;
  padding: 10px;
}

/* Botões Modernos */
.menuButton {
  font-family: 'Arial', sans-serif;
  font-size: 10px;
  font-weight: bold;
  padding: 10px 20px;
  border: 1px solid transparent;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  outline: none;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Botão Principal */
.primary {
  background-color: #fff;
  /* Cor do botão */
  color: #585858;
  border-color: #585858;
}

.primary:hover {
  background-color: #fff;
  /* Cor ao passar o mouse */
  color: #F78100;
  /* Texto em verde */
  border-color: #F78100;
}

/* Botão Secundário */
.secondary {
  background-color: #fff;
  /* Cor do botão */
  color: #585858;
  border-color: #585858;
}

.secondary:hover {
  background-color: #fff;
  /* Cor ao passar o mouse */
  color: #F78100;
  /* Texto em verde */
  border-color: #F78100;
}

.title {
  color: #F78100;
  font-size: 20px;
}

/*FIM MENU*/

.card {
  background-color: #ffffff;
  border-radius: 5px;
  border: 1px solid #F78100;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  padding: 14px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  height: 400px;
  width: 90%;
  /* Largura fixa */
}


/* Para telas maiores, como desktops */
@media (min-width: 768px) {
  .card {
    width: 88%;
    padding: 0px;
    height: 420px;
  }

  .numberItem {
    width: 160px;
    height: 160px;
  }
}
