.container {
  font-family: Roboto, sans-serif;
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.title {
  font-size: 1.2rem;
  color: #585858;
  text-align: center;
  margin-bottom: 20px;
}

.description {
  font-size: 1rem;
  color: #585858;
  line-height: 1.6;
  margin-bottom: 20px;
  text-align: justify;
}

.stepsTitle {
  font-size: 1rem;
  color: #c39a06;
  margin-bottom: 10px;
}

.stepsList {
  list-style-type: decimal;
  margin: 0;
  padding-left: 20px;
  font-size: 1rem;
  color: #585858;
  text-align: justify;
}

.stepsList li {
  margin-bottom: 10px;
}

.motivation {
  margin-top: 30px;
  background-color: #eafaf1;
  padding: 15px;
  border-left: 4px solid #F78100;
  border-radius: 4px;
}

.motivationTitle {
  font-size: 1rem;
  color: #F78100;
  margin-bottom: 10px;
}

.motivation p {
  font-size: 1rem;
  color: #585858;
  line-height: 1.6;
  text-align: justify;
}