/* COMPONENTES MEGASENA styles.css */

/** TELA GERAR PALPITES **/

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  border-radius: 5px;
  border: 1px solid #931d89;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  width: 360px;
  max-width: 420px;
  height: 480px;
  padding: 10px;
  box-sizing: border-box;
}

.select {
  margin-top: 20px;
  padding: 10px;
  border-radius: 5px;
  font-size: 12px;
  border: 1px solid #585858;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* Para telas maiores, como desktops */
@media (min-width: 768px) {
  .card {
    width: 300px;
  }
}

.numberBox {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  /* 10 itens por linha */
  gap: 8px;
  /* Espaçamento entre os itens */
  box-sizing: border-box;
  /* Inclui padding na largura */
  justify-self: center;
}

.numberBox>*:nth-child(n) {
  justify-self: center;
  /* Centraliza itens individuais */
}

.numberItem {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #931d89;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 16px;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 5px solid transparent;
  border-top: 3px solid #df43d2;
  border-right: 3px solid #931d89;
  border-radius: 50%;
  margin-top: -200px;
  animation: spin 1.5s linear infinite, color-change 3s infinite;
}

.numberBox.loading {
  display: flex;
  /* Centraliza usando flexbox quando o spinner está ativo */
  justify-content: center;
  align-items: center;
  width: 100%;
  /* O container ocupará toda a largura permitida */
  max-width: 400px;
  /* Largura máxima */
  min-height: 400px;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes color-change {
  0% {
    border-top-color: #eba2e5;
  }

  50% {
    border-top-color: #df43d2;
  }

  100% {
    border-top-color: #931d89;
  }
}

/****************** FINAL TELA GERAR PALPITES ***************** 
/*********************************************************
*********************************************************/



/** INICIO TELA PALPITES SALVOS **/

.listContainer {
  position: absolute;
  overflow-y: auto;
  background-color: transparent;
  max-height: 440px;
}

.content {
  background-color: #ffffff;
  border-radius: 5px;
  border: 1px solid #931d89;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  padding: 10px;
  width: 64%;
  height: auto;
  display: flex;
  justify-self: center;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
}

/* Para telas maiores, como desktops */
@media (min-width: 768px) {
  .content {
    width: 260px;
    height: auto;
  }

  .teste {
    background-color: #fff;
  }
}

.text {
  font-size: 14px;
  color: #585858;
  margin-bottom: 10px;
}

.numberList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.numberCircle {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #931d89;
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 14px;
}

.shareBtn {
  background: none;
  border: none;
  color: #ff1f1e;
  cursor: pointer;
  margin-top: 10px;
}

.share-button .material-icons {
  font-size: 12px;
}

.line {
  border: none;
  height: 1px;
  background-color: #ccc;
  margin: 10px 0;
}


.aguardandoPalpites {
  display: flex;
  justify-self: center;
  width: 90%;
  background-color: #ccc;
  margin-top: 60px;
  padding: 10px;
}